nav {
  background: -webkit-linear-gradient(left, #162618 0%, #162618 71%, #0e2310 99%);
  background: #162618;
  background: linear-gradient(to right, #162618 0%, #162618 71%, #0e2310 99%);
  color: #fff;
  font-family: 'Montserrat',sans-serif;
  height: 100vh;
  left: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  -webkit-transition: width .2s ease-in-out;
  transition: width .2s ease-in-out;
  width: 190px; }
  nav.small {
    width: 40px; }
    nav.small > .menu-item .sub-menu-indicator {
      opacity: 0; }
    nav.small ~ .content {
      padding-left: 40px;
      width: calc(100% - 40px); }
  nav.hidden {
    width: 0; }
  nav .menu-item {
    cursor: pointer;
    display: flex;
    font-family: 'Montserrat',sans-serif;
    font-size: 15px;
    overflow: hidden;
    padding: 0 2px;
    width: 190px; }
    nav .menu-item .menu-item-wrapper {
      margin: 0;
      padding: 0;
      position: relative;
      width: 100%; }
      nav .menu-item .menu-item-wrapper span {
        padding: 4px 8px;
        position: absolute; }
        nav .menu-item .menu-item-wrapper span:hover {
          text-decoration: underline; }
      nav .menu-item .menu-item-wrapper svg {
        overflow: hidden;
        padding: 6px;
        width: 20px !important; }
    nav .menu-item .sub-menu-indicator {
      position: absolute;
      right: 0.5em;
      padding: 0.5em;
      -webkit-transition: .26s ease-in-out;
      transition: .26s ease-in-out; }
      nav .menu-item .sub-menu-indicator svg {
        position: absolute;
        right: 0;
        -webkit-transition: .26s ease-in-out;
        transition: .26s ease-in-out; }
      nav .menu-item .sub-menu-indicator svg[data-icon='box-open'] {
        right: -.15em;
        opacity: 0; }
    nav .menu-item .sub-menu {
      margin: 30px 0 0 -190px;
      max-height: 0;
      overflow: hidden;
      -webkit-transition: max-height .3s ease-in-out;
      transition: max-height .3s ease-in-out; }
      nav .menu-item .sub-menu .menu-item {
        font-size: 13px; }

.content {
  font-family: 'Crete Round',serif;
  line-height: 1.5em;
  min-height: 100vh;
  overflow: hidden;
  padding-left: 190px;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
  width: calc(100% - 190px); }
  .content .section {
    clear: both;
    padding: .7em;
    border-bottom: 1px solid;
    border-color: #646464;
    border-color: rgba(150, 150, 150, 0.5);
    -webkit-background-clip: padding-box;
    /* for Safari */
    background-clip: padding-box;
    /* for IE9+, Firefox 4+, Opera, Chrome */ }
    .content .section[data-background='star'] {
      -webkit-animation: animatedBackground_bg 130s linear infinite;
              animation: animatedBackground_bg 130s linear infinite;
      background-color: #0f0f0f;
      background-image: url(data:image/gif;base64,);
      color: #e6e6e6 !important; }
      .content .section[data-background='star'] .section-image-wrapper {
